
.v-toast-wrap[data-v-3f045777] {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0);
  z-index: 9999;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.v-toast-box[data-v-3f045777] {
  min-width: 100px;
  max-width: 70%;
  min-height: 40px;
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 0;
}
/* 普通 */
.v-toast-common[data-v-3f045777] {
  -webkit-transform: scale(0.8) translateZ(0);
          transform: scale(0.8) translateZ(0);
  opacity: 0;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transform-origin: center center;
          transform-origin: center center;
}
.v-toast-common.v-toast-box-show[data-v-3f045777] {
  opacity: 1;
  -webkit-transform: scale(1) translateZ(0);
          transform: scale(1) translateZ(0);
}
/* 上出来 */
.v-toast-box.v-toast-box-top[data-v-3f045777] {
  position: absolute;
  top: -20%;
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.v-toast-box.v-toast-box-top.v-toast-box-show[data-v-3f045777] {
  top: 6%;
  opacity: 1;
}
/* 下出来 */
.v-toast-box.v-toast-box-bottom[data-v-3f045777] {
  position: absolute;
  bottom: -20%;
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.v-toast-box.v-toast-box-bottom.v-toast-box-show[data-v-3f045777] {
  bottom: 6%;
  opacity: 1;
}
/* 内容 */
.v-toast-box .v-toast-title[data-v-3f045777] {
  font-size: 14px;
}
.v-toast-box .v-toast-title.limitTitle[data-v-3f045777] {
  max-width: 100px;
}
.v-toast-box .v-toast[data-v-3f045777] {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  font-size: 0;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.v-toast-box .v-toast-success[data-v-3f045777] {
  background: transparent
    url(data:image/png;base64,)
    no-repeat;
  background-size: 100% 100%;
}
.v-toast-box .v-toast-warning[data-v-3f045777] {
  background: transparent
    url(data:image/png;base64,)
    no-repeat;
  background-size: 100% 100%;
}



